}

:root {
  --color-text-main: #000;
  --color-text-sec: #fff;
  --color-text-third: #e20a17;
  --color-text-h: #fff100;
  --color-text-header: #004682;
  --color-text-p: #545454;
  --color-bg-main: #fff;
  --color-bg-sec: #f7f7f7;
  --blue: #2196f3;
  --white: #fff;
  --contact-icon: #afb1b8;
  --font-family: "Roboto", sans-serif;
  --font-weight: 500;
  --font-header: "Noto Sans", sans-serif;
  --font-regular: 400;
  --font-medium: 500;
  --font-baner: "Crimson Text", serif;
  --font-size-main: 12px;
  --font-size-sec: 20px;
  --font-size-third: 14px;
  --font-size-fourth: 50px;
  --border-radius: 5px;
  --transition-duration: .25s;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.is-hidden {
  white-space: nowrap;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.visually-hidden {
  opacity: 0;
  pointer-events: none;
  transition: opacity var(--transition-duration) var(--timing-function);
}

.backdrop {
  z-index: 100;
  transition: opacity var(--transition-duration) var(--timing-function);
  background: #afb1b81a;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

button.active-btn {
  background-color: var(--color-accent-sec);
  border-color: var(--color-accent-sec);
  color: var(--color-text-sec);
}

.d-none {
  display: none;
}

@media screen and (min-width: 767px) {
  .d-none-mobile {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .d-none-tablet {
    display: none;
  }
}

@media screen and (min-width: 1280px) {
  .d-none-desktop {
    display: none;
  }
}

.title {
  font-family: var(--font-family);
  color: var(--color-text-third);
  text-shadow: 1px 1px 2px #00000080;
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 5vw;
  font-weight: 700;
  line-height: 1;
}

@media screen and (min-width: 768px) {
  .title {
