  }
}

.title--footer {
  font-size: 3vw;
}

@media screen and (min-width: 768px) {
  .title--footer {
    font-size: 1.5vw;
  }

  .title--kontakt {
    padding-left: 15px;
    font-size: 20px;
  }
}

.paragraph {
  font-size: var(--font-size-sec);
  color: var(--color-text-p);
  font-style: var(--font-header);
  font-weight: var(--font-medium);
  text-align: justify;
  line-height: 1.5;
}

.paragraph--karate {
  font-size: 2.5vw;
}

@media screen and (min-width: 768px) {
  .paragraph--karate {
