  }
}

.paragraph--footer {
  color: var(--color-text-sec);
  text-align: center;
  font-size: 3vw;
}

@media screen and (min-width: 768px) {
  .paragraph--footer {
    font-size: 1vw;
  }
}

.paragraph--baner {
  color: var(--color-text-p);
  text-align: left;
  font-size: 3vw;
}

@media screen and (min-width: 768px) {
  .paragraph--baner {
    font-size: 2vw;
  }
}

* {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

main {
  flex: 1;
}

footer {
  flex-shrink: 0;
}

ul, li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

button {
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 14px;
  height: 18px;
}

::-webkit-scrollbar-thumb {
  background-color: #0003;
  background-clip: padding-box;
  border: 4px solid #0000;
  -webkit-border-radius: 7px;
  height: 6px;
  -webkit-box-shadow: inset -1px -1px #0000000d, inset 1px 1px #0000000d;
}

::-webkit-scrollbar-button {
  width: 0;
  height: 0;
  display: none;
}

::-webkit-scrollbar-corner {
  background-color: #0000;
}

body {
