  background-color: var(--color-bg-main);
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.header {
  color: var(--color-text-main);
  z-index: 1000;
  text-align: center;
  background-color: #ffffff4d;
  width: 100%;
  height: 100px;
  padding: 10px;
  transition: background-color .3s;
  position: fixed;
  top: 5%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header-wraper {
  align-items: center;
  margin: 0 auto;
  display: flex;
}

@media screen and (min-width: 480px) {
  .header-wraper {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .header-wraper {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .header-wraper {
    width: 1280px;
  }
}

.nav-box {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 80px;
  max-width: 900px;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.logo {
  flex-shrink: 0;
}

.nav {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 470px;
  height: 100px;
  display: flex;
  overflow: hidden;
}

.nav__item {
  display: inline-block;
}

.nav__link {
  font-size: var(--font-size-sec);
  color: var(--color-text-header);
  font-style: var(--font-header);
  font-weight: var(--font-medium);
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: var(--cubic-bezier);
  line-height: 1.14;
  list-style-type: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: var(--white);
  cursor: pointer;
  background-color: #004682;
  margin: -10px;
  padding: 10px;
}

.nav__icon {
  fill: var(--color-text-header);
  transition-property: fill background-color;
  transition-duration: .25s;
  transition-timing-function: var(--timing-function);
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.nav__icon:hover {
  color: var(--blue);
  fill: var(--white);
  cursor: pointer;
  background-color: #004682;
}

.nav-modal {
  width: 24px;
  height: 24px;
}

.dropdown {
  z-index: 1;
  margin: 0, auto;
  background-color: #ffffff4d;
  flex-direction: column;
  width: 120px;
  height: max-content;
  margin-top: 10px;
  display: none;
  position: absolute;
}

.nav__item:hover .dropdown {
  display: block;
}

.dropdown li {
  margin-bottom: 10px;
  margin-left: 0;
  margin-right: 0;
}

.mobile-menu {
  z-index: 10;
  background-color: #fff;
  width: 200px;
  height: 100%;
  padding-top: 60px;
  transition: transform .3s ease-in-out;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.mobile-menu__list {
  padding-top: 50px;
}

.mobile-menu__item {
  padding-block: 12px;
  transition-property: border;
  transition-duration: .25s;
  transition-timing-function: var(--cubic-bezier);
  border-top: 1px solid #004682;
  padding-left: 21px;
}

.mobile-menu__item:hover, .mobile-menu__item:focus {
  border: 1px solid #004682;
}

.mobile-menu__item:last-child {
  border-bottom: 1px solid #004682;
}

.mobile-menu__link {
  font-size: var(--font-size-sec);
  color: var(--color-text-header);
  font-style: var(--font-header);
  font-weight: var(--font-medium);
  text-align: right;
  margin-left: 10px;
  line-height: 1.14;
}

.mobile-menu__link--dropdown {
  font-size: var(--font-size-third);
  color: var(--color-text-header);
  font-style: var(--font-header);
  padding-left: 5px;
  line-height: 1.14;
}

.mobile-menu__button {
  fill: #004682;
  background-color: #fff0;
  border: none;
  justify-content: flex-end;
  width: 24px;
  height: 24px;
  padding: 0;
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.mobile-menu__button:hover svg {
  fill: var(--blue);
  cursor: pointer;
}

